@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap');

/* RESET CSS */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed, 
figure, figcaption, footer, header, hgroup, 
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
	margin: 0;
	padding: 0;
	border: 0;
	font-size: 100%;
	font: inherit;
	vertical-align: baseline;
}
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	font-family: 'Montserrat', 'Noto Sans KR', sans-serif;
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

//Header
header {
	position: fixed;
	width: 100%;
	top: 0;
	z-index: 100;
}

nav {
	ul {
		width: 50%;
		display: flex;
		margin: 0 auto;
		justify-content: space-evenly;
		align-items: center;

		li a{
			text-decoration: none;
			color: #44b7b6;
			font-weight: bold;
			font-size: 24px;
			line-height: 70px;
			position: relative;
			
			&::after {
				content: '';
				position: absolute;
				width: 100%;
				height: 2px;
				background-color:#44b7b6;
				display: block;
				bottom: -8px;
				transition: .4s ease-in-out;
				opacity: 0;
				}
		
			&:hover::after{
				opacity: 1;
			}

		}

	}
}

//visual 

@keyframes visualMove {
	from {
		transform: scale(1);
	} to {
		transform: scale(1.07);
	}
}
.visual {
	height: 100vh;
	overflow: hidden;
	position: relative;

	img {
		display: block;
		width: 100%;
		animation: visualMove 4s forwards;
	}

	h1 {
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translateX(-50%) translateY(-50%);
		line-height: 1.2;
		font-weight: 900;
		font-size: 35px;
		color: black;
	}
}


//profile 

@keyframes fadeIn {
	from {
		opacity: 0;
	} to {
		opacity: 1;
	}
}
@keyframes leftFadeIn {
	from {
		opacity: 0;
		left: -100%;
	} to {
		opacity: 1;
		left: 0;
	}
}


@keyframes slideUp {
	from {
		opacity: 0;
		top: 5vh;
	} to {
		opacity: 1;
		top: 0;
	}
}

.profile {
	display: flex;
	height: 100vh;
	overflow-y: hidden;
	align-items: center;
	
	.profileImg{
		width: 50%;
		img {
		position: relative;
		display: block;
		width: 62%;
		margin: 0 auto;
		animation: leftFadeIn 1s forwards ease-in-out;
	}
	}

	.profileDesc {
		display: flex;
		flex-direction: column;
		width: 40%;
		h2 {
			position: relative;
			font-size: 50px;
			font-weight: bold;
			animation: 1s ease-in-out slideUp forwards;
			padding-bottom: 30px;
			border-bottom: 1px solid rgba(204, 204, 204, 0.5);
			color: #44b7b6;
			span {
				font-weight: 300;
				font-size: 30px;
			}
		}

		>div:first-of-type {
			ul{
				animation: 1s ease-in-out slideUp forwards;
				animation-delay: .3s;
				opacity: 0;
				margin-bottom: 120px;
			}
		}

		>div:last-of-type {
			ul{
				animation: 1s ease-in-out slideUp forwards;
				animation-delay: .7s;
				opacity: 0;
			}
		}

		h3 {
			position: relative;
			font-size: 30px;
			font-weight: bold;
			animation: 1s ease-in-out slideUp forwards;
				animation-delay: .5s;
				opacity: 0;
				color: #44b7b6;
		}

		ul {
			position: relative;
			margin: 30px 0 0;
		}

		li {
			display: flex;
			line-height: 2;
			font-size: 24px;
			span {
				width: 20%;
				font-weight: 500;
				color: #333;
			}

			div {
				width: 5%;
			}
			img {
				width: 24px;
				vertical-align: middle;
			}

			a {
				color: black;
				text-decoration:underline 1px;

				&:hover {
					color: rgb(110, 1, 110);
				}
			}
		}
	}
}

// filmography 
.filmoDesc {
	>ul {
	display: flex;
	flex-wrap: wrap;
	justify-content: space-evenly;
	width: 80%;
	margin: 100px auto;
		>li {
			width: 40%;
			margin-bottom: 100px;
			li {
		font-size: 20px;
		line-height: 2.5;
		display: flex;
		color: #333;
		.year {
			font-weight: bold;
			width: 30%;
			text-align: center;
		}
		}
	}
}
	h2 {
		padding-left: 20px;
		font-size: 50px;
		font-weight: 900;
		font-style: italic;
		margin: 30px 0;
		color: #44b7b6;
	}

	.movie {
		position: relative;
		animation: 1s ease-in-out slideUp forwards;
	}

	.drama {
		opacity: 0;
		position: relative;
		animation: 1s ease-in-out slideUp forwards;
		animation-delay: .3s;
	}

	.cf {
		opacity: 0;
		position: relative;
		animation: 1s ease-in-out slideUp forwards;
		animation-delay: .5s;
	}

	.play {
		opacity: 0;
		position: relative;
		animation: 1s ease-in-out slideUp forwards;
		animation-delay: .7s;
	}
	
}

//videos 
.videos {
	display: flex;
	flex-wrap: wrap;
	width: 90%;
	margin: 100px auto;
	justify-content: space-evenly;
	position: relative;

	.listOuter {
		opacity: 0;
		position: relative;
		animation: 1s ease-in-out slideUp forwards;
		width: 46%;
		margin-bottom: 2%;
		box-shadow: 0 0 40px rgba(0, 0, 0, 0.219);

		&:nth-of-type(3),
		&:nth-of-type(4),
		&:nth-of-type(5) {
			width: 29.7%;
		}
		}
	.videoList {
		padding:56.25% 0 0 0;
		position:relative;

		iframe {
			position:absolute;
			top:0;
			left:0;
			width:100%;
			height:100%;
		}
	}
}


//gallery 

	.gallerylist {
		width: 90%;
		margin: 100px auto;
		padding-top: 30px;
   column-count: 5;
   column-gap: 30px;
	 opacity: 0;
		position: relative;
		animation: 1s ease-in-out slideUp forwards;

	 >div {
   display: inline-block;
   width: 100%;
	 margin-top: 15px;
   margin-bottom: 15px;
   position: relative;
	 box-shadow: 0 0 15px rgba(0, 0, 0, 0.10);

	 &:hover {
		 filter: saturate(150%);
	 }

	 img{
   display: block;
   width: 100%;
	 
}
}
}
