@import url("https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Noto+Sans+KR:wght@100;300;400;500;700;900&display=swap");
/* RESET CSS */
html, body, div, span, applet, object, iframe,
h1, h2, h3, h4, h5, h6, p, blockquote, pre,
a, abbr, acronym, address, big, cite, code,
del, dfn, em, img, ins, kbd, q, s, samp,
small, strike, strong, sub, sup, tt, var,
b, u, i, center,
dl, dt, dd, ol, ul, li,
fieldset, form, label, legend,
table, caption, tbody, tfoot, thead, tr, th, td,
article, aside, canvas, details, embed,
figure, figcaption, footer, header, hgroup,
menu, nav, output, ruby, section, summary,
time, mark, audio, video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}

article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {
  display: block;
}

body {
  font-family: "Montserrat", "Noto Sans KR", sans-serif;
  line-height: 1;
}

ol, ul {
  list-style: none;
}

blockquote, q {
  quotes: none;
}

blockquote:before, blockquote:after,
q:before, q:after {
  content: "";
  content: none;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

header {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 100;
}

nav ul {
  width: 50%;
  display: flex;
  margin: 0 auto;
  justify-content: space-evenly;
  align-items: center;
}
nav ul li a {
  text-decoration: none;
  color: #44b7b6;
  font-weight: bold;
  font-size: 24px;
  line-height: 70px;
  position: relative;
}
nav ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  background-color: #44b7b6;
  display: block;
  bottom: -8px;
  transition: 0.4s ease-in-out;
  opacity: 0;
}
nav ul li a:hover::after {
  opacity: 1;
}

@keyframes visualMove {
  from {
    transform: scale(1);
  }
  to {
    transform: scale(1.07);
  }
}
.visual {
  height: 100vh;
  overflow: hidden;
  position: relative;
}
.visual img {
  display: block;
  width: 100%;
  animation: visualMove 4s forwards;
}
.visual h1 {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translateX(-50%) translateY(-50%);
  line-height: 1.2;
  font-weight: 900;
  font-size: 35px;
  color: black;
}

@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes leftFadeIn {
  from {
    opacity: 0;
    left: -100%;
  }
  to {
    opacity: 1;
    left: 0;
  }
}
@keyframes slideUp {
  from {
    opacity: 0;
    top: 5vh;
  }
  to {
    opacity: 1;
    top: 0;
  }
}
.profile {
  display: flex;
  height: 100vh;
  overflow-y: hidden;
  align-items: center;
}
.profile .profileImg {
  width: 50%;
}
.profile .profileImg img {
  position: relative;
  display: block;
  width: 62%;
  margin: 0 auto;
  animation: leftFadeIn 1s forwards ease-in-out;
}
.profile .profileDesc {
  display: flex;
  flex-direction: column;
  width: 40%;
}
.profile .profileDesc h2 {
  position: relative;
  font-size: 50px;
  font-weight: bold;
  animation: 1s ease-in-out slideUp forwards;
  padding-bottom: 30px;
  border-bottom: 1px solid rgba(204, 204, 204, 0.5);
  color: #44b7b6;
}
.profile .profileDesc h2 span {
  font-weight: 300;
  font-size: 30px;
}
.profile .profileDesc > div:first-of-type ul {
  animation: 1s ease-in-out slideUp forwards;
  animation-delay: 0.3s;
  opacity: 0;
  margin-bottom: 120px;
}
.profile .profileDesc > div:last-of-type ul {
  animation: 1s ease-in-out slideUp forwards;
  animation-delay: 0.7s;
  opacity: 0;
}
.profile .profileDesc h3 {
  position: relative;
  font-size: 30px;
  font-weight: bold;
  animation: 1s ease-in-out slideUp forwards;
  animation-delay: 0.5s;
  opacity: 0;
  color: #44b7b6;
}
.profile .profileDesc ul {
  position: relative;
  margin: 30px 0 0;
}
.profile .profileDesc li {
  display: flex;
  line-height: 2;
  font-size: 24px;
}
.profile .profileDesc li span {
  width: 20%;
  font-weight: 500;
  color: #333;
}
.profile .profileDesc li div {
  width: 5%;
}
.profile .profileDesc li img {
  width: 24px;
  vertical-align: middle;
}
.profile .profileDesc li a {
  color: black;
  text-decoration: underline 1px;
}
.profile .profileDesc li a:hover {
  color: rgb(110, 1, 110);
}

.filmoDesc > ul {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-evenly;
  width: 80%;
  margin: 100px auto;
}
.filmoDesc > ul > li {
  width: 40%;
  margin-bottom: 100px;
}
.filmoDesc > ul > li li {
  font-size: 20px;
  line-height: 2.5;
  display: flex;
  color: #333;
}
.filmoDesc > ul > li li .year {
  font-weight: bold;
  width: 30%;
  text-align: center;
}
.filmoDesc h2 {
  padding-left: 20px;
  font-size: 50px;
  font-weight: 900;
  font-style: italic;
  margin: 30px 0;
  color: #44b7b6;
}
.filmoDesc .movie {
  position: relative;
  animation: 1s ease-in-out slideUp forwards;
}
.filmoDesc .drama {
  opacity: 0;
  position: relative;
  animation: 1s ease-in-out slideUp forwards;
  animation-delay: 0.3s;
}
.filmoDesc .cf {
  opacity: 0;
  position: relative;
  animation: 1s ease-in-out slideUp forwards;
  animation-delay: 0.5s;
}
.filmoDesc .play {
  opacity: 0;
  position: relative;
  animation: 1s ease-in-out slideUp forwards;
  animation-delay: 0.7s;
}

.videos {
  display: flex;
  flex-wrap: wrap;
  width: 90%;
  margin: 100px auto;
  justify-content: space-evenly;
  position: relative;
}
.videos .listOuter {
  opacity: 0;
  position: relative;
  animation: 1s ease-in-out slideUp forwards;
  width: 46%;
  margin-bottom: 2%;
  box-shadow: 0 0 40px rgba(0, 0, 0, 0.219);
}
.videos .listOuter:nth-of-type(3), .videos .listOuter:nth-of-type(4), .videos .listOuter:nth-of-type(5) {
  width: 29.7%;
}
.videos .videoList {
  padding: 56.25% 0 0 0;
  position: relative;
}
.videos .videoList iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

.gallerylist {
  width: 90%;
  margin: 100px auto;
  padding-top: 30px;
  column-count: 5;
  column-gap: 30px;
  opacity: 0;
  position: relative;
  animation: 1s ease-in-out slideUp forwards;
}
.gallerylist > div {
  display: inline-block;
  width: 100%;
  margin-top: 15px;
  margin-bottom: 15px;
  position: relative;
  box-shadow: 0 0 15px rgba(0, 0, 0, 0.1);
}
.gallerylist > div:hover {
  filter: saturate(150%);
}
.gallerylist > div img {
  display: block;
  width: 100%;
}

